.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero__image {
  max-width: 100vw;
  width: 70vw;
  position: relative;
}
.image {
  width: 100%;
}

.description {
  margin-top: 2rem;
}
.description p:first-child {
  font-size: 3rem;
  font-weight: bold;
  color: var(--textColor);
}
.description p:nth-child(2) {
  font-size: 2rem;
  font-weight: thin;
  color: var(--textLightColor);
}
.icons__wrapper {
  background: var(--main);
  width: max-content;
  margin: 1rem auto;
  padding: 0.75rem 2rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.icon {
  color: var(--White);
  font-size: 4rem !important;
}
.icon:hover {
  transform: scale(1.1);
}
.scroll__circles {
  width: 11rem;
  height: 1.9rem;
  margin: 2rem auto;
  display: block;
  border-radius: 1rem;
  border: 0.2rem solid #7d62f2;
  background: none;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: relative;
  color: transparent;
}

.circle1 {
  position: absolute;
  height: 1.5rem;
  width: 1.5rem;
  background: var(--main);
  border-radius: 50%;
  animation: circleJump1 1s infinite alternate-reverse;
}
.circle2 {
  position: absolute;
  height: 1.5rem;
  width: 1.5rem;
  background: var(--secondary);
  border-radius: 50%;
  animation: circleJump2 1s infinite alternate-reverse;
}
@keyframes circleJump1 {
  from {
    left: 0;
  }

  to {
    left: 36%;
    opacity: 0.9;
  }
}
@keyframes circleJump2 {
  from {
    right: 0;
  }

  to {
    right: 36%;
    opacity: 0.9;
  }
}
@media screen and (min-width: 1000px) {
  .hero {
    flex-direction: row-reverse;
    justify-content: space-around;
  }
  .hero__image {
    width: 40vw;
  }
  .description p:first-child {
    font-size: 6.3rem;
  }
  .description p:nth-child(2) {
    font-size: 4rem;
  }
  .scroll__circles {
    display: none;
  }
}
